li.current {
    @apply bg-blue-100 rounded-r-full text-blue-500 font-bold;
}

li.current:hover {
    @apply bg-opacity-80;
}

table.contacts tr:hover .actions {
    opacity: 1;
}

::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    background-color: #ddd;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #555;
}

::-webkit-scrollbar-track {
    border-radius: 10px;
}